import React, { useState, useEffect } from 'react'
import "./Helico.css"

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLocationDot } from '@fortawesome/free-solid-svg-icons'
import { faRocket } from '@fortawesome/free-solid-svg-icons'
import { faShuttleSpace } from '@fortawesome/free-solid-svg-icons'
import { faHelicopter } from '@fortawesome/free-solid-svg-icons'

import town1 from "../../Assets/Mission/hel1.png"
import town2 from "../../Assets/Mission/hel.png"
import pilote from "../../Assets/Mission/pilote2.png"
import piloteye from "../../Assets/Mission/piloteye.png"
import piloteno from "../../Assets/Mission/piloteno.png"

import helico from "../../Assets/Mission/helico.png"

export default function Helico(props) {

    const [adresse, setAdresse] = useState(false)
    const [value, setValue] = useState("")

    useEffect(() => {

        let temp = value.toLowerCase()

        if (

            temp.includes("houtman") &&
            temp.includes("abrolhos") &&
            // value.includes("wa") &&
            // value.includes("6530") &&
            temp.includes("australie")

        ) {
            // setTakeOff(true)
            setAdresse(true)
            // setTimeout(() => {
            //     setSwitchStarsOn(true)
            // }, 12000)

        }

    }, [value])

    return (
        <div className='heli_main'
        // onClick={() => {
        //     setAdresse(true)
        // }}
        >
            <div className='heli_main2'>
                <img
                    className='heli_town'
                    src={adresse ? town2 : town1}
                />
                {/* <FontAwesomeIcon className={adresse ? "heli_icc2" : "heli_icc"} icon={faHelicopter} /> */}
                <img
                    src={helico}
                    style={
                        adresse ?
                            {
                                position: "absolute",
                                bottom: "2%",
                                left: "12%",
                                width: "80%",
                                transition: "all 7s ease-in",
                                transform: "translate(300px, -600px) scaleX(-1)",
                                opacity: "0",
                            }
                            :
                            {
                                position: "absolute",
                                bottom: "2%",
                                left: "12%",
                                width: "80%",
                                transform: "scaleX(-1)",
                            }

                    }
                />

                <img
                    className='pilote'
                    src={value === "" ? pilote : adresse ? piloteye : piloteno}
                />
                <input
                    className='heli_input'
                    type="text"
                    value={value}
                    onChange={(e) => {
                        setValue(e.target.value)

                    }}
                    placeholder='DESTINATION'
                >
                </input>
            </div>

            <button
                className='btn_retour_heli'
                onClick={() => {
                    props.setToggleMissionOne(false)
                    props.setPanneauValue("garage")
                }}
            >
                Retour
            </button>

        </div>
    )
}
