import React from 'react'
import './MissionSpecifique.css'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLocationDot } from '@fortawesome/free-solid-svg-icons'
import { faRocket } from '@fortawesome/free-solid-svg-icons'
import { faShuttleSpace } from '@fortawesome/free-solid-svg-icons'
import { faHelicopter } from '@fortawesome/free-solid-svg-icons'
import { faFeatherPointed } from '@fortawesome/free-solid-svg-icons'
import Fusee from './Fusee'
import Helico from './Helico'
import Canard from './Canard'

export default function MissionSpecifique(props) {

    let iconFA = ""
    // eslint-disable-next-line default-case
    switch (props.icon) {
        case 'faRocket':
            iconFA = faRocket;
            break;
        case 'faShuttleSpace':
            iconFA = faShuttleSpace;
            break;
        case 'faHelicopter':
            iconFA = faHelicopter;
            break;
        case 'faLocationDot':
            iconFA = faLocationDot;
            break;
        case 'faFeatherPointed':
            iconFA = faFeatherPointed;
            break;
    }

    return (
        <>
            {/* <div className="outside_container"> */}
            {/* <div className="container_mission"> */}
            {/* <h2>Hi {props.icon}</h2> */}
            {/* <FontAwesomeIcon className={props.icon + " icone_alone"} icon={iconFA} /> */}

            {/* </div> */}

            {props.icon === 'faRocket' && <Fusee setPanneauValue={props.setPanneauValue} setToggleMissionOne={props.setToggleMissionOne}/>}
            {props.icon === 'faHelicopter' && <Helico  setPanneauValue={props.setPanneauValue} setToggleMissionOne={props.setToggleMissionOne}/>}
            {props.icon === 'faFeatherPointed' && <Canard  setPanneauValue={props.setPanneauValue} setToggleMissionOne={props.setToggleMissionOne}/>}
            {/* </div> */}
        </>)
}
