import React, { useState, useEffect } from 'react'
import "./Restau.css"

export default function Restau(props) {

  let k = 680 - 4

  const [menu, setMenu] = useState("")
  const [plat, setPlat] = useState("")

  const [menuOk, setMenuOk] = useState(false)
  const [platOk, setPlatOk] = useState(false)

  let w1 = "a"
  let w4 = "t"
  let w2 = "au"
  let w5 = "tion"
  let w3 = "des"
  w1 += "r"
  w2 += "to"
  w3 += "truc"
  let p = Math.sqrt(k)

  useEffect(() => {

    let menuTemp = menu.toLowerCase()
    // console.log(menuTemp)

    if (

      menuTemp.includes(w1 + w4) &&
      menuTemp.includes(w2) &&
      menuTemp.includes(w3 + w5)

    ) {
      setMenuOk(true)
    }

  }, [menu])

  useEffect(() => {

    let platTemp = plat.toLowerCase()
    // console.log(menuTemp)

    if (

      platTemp.includes("homard")

    ) {
      setPlatOk(true)
    }

  }, [plat])


  return (
    <div className='restau_main1'>
      <div className='restau_main2'>

        <div className='square1'></div>
        <div className='square2'>
          <p className='txt1'>Au Volcan !</p>
        </div>

        <div className={(platOk && menuOk) ? 'square3bisv' : 'square3bis'}>
          <p>{"Vos hôtes vous attendent page " + p + " !!"}</p>
        </div>

        <div className={(platOk && menuOk) ? 'square3v' : 'square3'}>
          <p className='txt2'>Menu</p>
          <input
            className={menuOk ? 'input1v' : 'input1'}
            type='text'
            value={menu}
            onChange={(e) => {
              setMenu(e.target.value)
            }}
          >
          </input>
          <p className='txt3'>Plat</p>
          <input
            className={platOk ? 'input2v' : 'input2'}
            type='text'
            value={plat}
            onChange={(e) => {
              setPlat(e.target.value)
            }}

          >
          </input>


        </div>

      </div>

      <button
        className='btn_retour_restau'
        onClick={() => {
          props.changeToggle(false)
          props.setPanneau(true)
        }}
      >
        Retour
      </button>
    </div>
  )
}
