import React, { useState } from 'react'
import "./Creperie.css"
import emailjs from '@emailjs/browser';

export default function Creperie(props) {

  const [value, setValue] = useState("")

  return (
    <div className='creperie_main1'>
      <div className='creperie_main2'>

        <div className='Csquare1'></div>
        <div className='Csquare2'>
          <p className='Ctxt1'>Créperie</p>
        </div>

        <div className='Csquare3'>

          <p className='Ctxt2'>Une Affaire de famille</p>

          <textarea className='Cinput2'
          value={value}
            onChange={(e) => {
              setValue(e.target.value)
            }}
          >

          </textarea>

        </div>


        <button className='btn1'
          onClick={() => {
            console.log(value)
            emailjs.send("service_u9yptul", 'template_qpabx6p', { message: value }, 'wMddJ1RsZbklmlc2J')
              .then((result) => {
                console.log(result.text);
                setValue("")
                alert("Message envoyé !")
              }, (error) => {
                console.log(error.text);
              });
          }}
        >
          Valider la recette
        </button>

      </div>

      <button
        className='btn_retour_creperie'
        value={value}
        onClick={() => {
          props.changeToggle(false)
          props.setPanneau(true)
        }}
      >
        Retour
      </button>
    </div>
  )
}
