import React, { useState } from 'react'
import fusee from "../../Assets/Mission/fusee.webp"
import fuseeTransparent from "../../Assets/Mission/fusee.png"
import fond from "../../Assets/Mission/fond.png"
import "./Fusee.css"

export default function Fusee(props) {

    const [takeOff, setTakeOff] = useState(false)
    const [value, setValue] = useState("")
    const [switchStarsOn, setSwitchStarsOn] = useState(false)

    return (
        <div className='cockpit_wrap_exte'>
            <div className='cockpit_wrap'>
                <img
                    className={takeOff ? 'cockpit_fusee2_2' : 'cockpit_fusee2'}
                    alt="fusee"
                    // src={fusee}
                    // src={fuseeTransparent}
                    // src={"https://image.shutterstock.com/shutterstock/photos/99106808/display_1500/stock-photo-spaceship-on-martian-landscape-illustration-of-a-cartoon-spaceship-landing-on-martian-red-desert-99106808.jpg"}
                    src={fond}
                />
                <img
                    className='cockpit_fusee'
                    alt="fusee"
                    // src={fusee}
                    src={fuseeTransparent}
                // onClick={() => {
                //     setTakeOff(true)
                // }}
                />
                <input
                    className='cockpit_input'
                    type="text"
                    value={value}
                    onChange={(e) => {
                        setValue(e.target.value)
                        if (e.target.value === "X36H") {
                            setTakeOff(true)
                            setTimeout(() => {
                                setSwitchStarsOn(true)
                            }, 8000)

                        }
                    }}
                // placeholder='AKZH'
                >

                </input>

                {/* <input
                        className='cockpit_input1'
                        type="text"
                        value="Page"
                    // placeholder='AKZH'
                    >

                    </input> */}

                {
                    switchStarsOn &&
                    <>
                        <div className="star1 stars"></div>
                        <div className="star2 stars"></div>
                        <div className="star3 starsA"></div>
                        <div className="star4 stars"></div>
                        <div className="star5 stars"></div>
                        <div className="star6 starsA"></div>
                        <div className="star7 stars"></div>
                        <div className="star8 stars"></div>
                        <div className="star9 stars"></div>
                        <div className="star10 starsB"></div>
                        <div className="star11 starsA"></div>
                        <div className="star12 stars"></div>
                        <div className="star13 stars"></div>
                        <div className="star14 stars"></div>
                        <div className="star15 starsA"></div>
                        <div className="star16 stars"></div>
                        <div className="star17 stars"></div>
                        <div className="star18 starsB"></div>
                        <div className="star19 stars"></div>
                        <div className="star20 stars"></div>
                        <div className="star21 starsB"></div>
                        <div className="star22 stars"></div>
                        <div className="star23 stars"></div>
                        <div className="star24 starsA"></div>
                        <div className="star25 stars"></div>
                        <div className="star26 stars"></div>
                        <div className="star27 stars"></div>
                        <div className="star28 starsB"></div>
                        <div className="star29 starsA"></div>
                        <div className="star30 starsB"></div>

                    </>
                }

            </div>

            <button
            className='btn_retour_fusee'
                onClick={() => {
                    props.setToggleMissionOne(false)
                    props.setPanneauValue("garage")
                }}
            >
                Retour
            </button>

        </div>
    )
}
