import React from 'react'
import "./Panneau.css"

import panneau from "../../Assets/Menu/signboard_base.png"

export default function Panneau(props) {
  return (
    <div className='panneau_main1'>
      <div className='panneau_main2'>

        <img
        className='panneau_img'
          src={panneau}
          alt=""
        />

        <button
          className='btn_garage btn'
          onClick={() => {
            props.setPanneauValue("garage")
            props.setPanneau(false)
          }}
        >Garage</button>

        <button
          className='btn_restau btn'
          onClick={() => {
            props.setPanneauValue("restau")
            props.setPanneau(false)
          }}
        >Restau</button>

        <button
          className='btn_créperie btn'
          onClick={() => {
            props.setPanneauValue("creperie")
            props.setPanneau(false)
          }}
        >Créperie</button>

        <button
          className='btn_help btn'
          onClick={() => {
            props.setPanneauValue("help")
            props.setPanneau(false)
          }}
        >Help !</button>

      </div>
    </div>
  )
}
