import React from 'react'
import "./Canard.css"
import Minigame from './Minigame'

export default function Canard(props) {
    return (
        <div className='canard_main1'>
            <div className='canard_main2'>




                <Minigame />





            </div>
            <button
                className='btn_retour_canard'
                onClick={() => {
                    props.setToggleMissionOne(false)
                    props.setPanneauValue("garage")
                }}
            >
                Retour
            </button>
        </div>
    )
}
