import './App.css';

import React from 'react'
import FrontPage from './Components/FrontPage/FrontPage'
import Entree from './Components/Entree/Entree'
import Garage from './Components/Garage/Garage'
import MissionSpecifique from './Components/Garage/MissionSpecifique';
import { useState } from 'react';
import Panneau from './Components/Panneau/Panneau';
import Restau from './Components/Restau/Restau';
import Creperie from './Components/Creperie/Creperie';

function App() {

  const [toggleFront, setToggleFront] = useState(true);
  const [toggleEntree, setToggleEntree] = useState(false);
  const [toggleGarage, setToggleGarage] = useState(true);
  // const [toggleMissionOne, setToggleMissionOne] = useState("faShuttleSpace");
  const [toggleMissionOne, setToggleMissionOne] = useState(false);

  const [panneau, setPanneau] = useState(false);
  // const [panneauValue, setPanneauValue] = useState("garage");
  const [panneauValue, setPanneauValue] = useState("");

  return (
    <>
      {toggleFront && <FrontPage changeToggle={setToggleFront} changeNextToggle={setToggleEntree} />}
      {toggleEntree ? <Entree changeToggle={setToggleEntree} changeNextToggle={setPanneau} /> : ""}

      {panneau && <Panneau setPanneauValue={setPanneauValue} setPanneau={setPanneau} />}

      {panneauValue === "garage" && <Garage setPanneau={setPanneau} changeToggle={setPanneauValue} changeNextToggle={setToggleMissionOne} />}
      {panneauValue === "restau" && <Restau setPanneau={setPanneau} changeToggle={setPanneauValue} />}
      {panneauValue === "creperie" && <Creperie setPanneau={setPanneau} changeToggle={setPanneauValue} />}



      {toggleMissionOne ? <MissionSpecifique icon={toggleMissionOne} setPanneauValue={setPanneauValue} setToggleMissionOne={setToggleMissionOne} /> : ""}
    </>
  )
}

export default App;